import React from "react";
import { Page, Tabbar, Tab, } from "react-onsenui";
import Dashboard from "../dashboard/Dashboard";
import Trips from "../trip/list/TripsContainer";
import VehicleList from "../vehicle/list/VehicleList";
import Cost from "../cost/CostContainer";
import About from "../about/About";

class Main extends React.Component {
  renderTabs() {
    return [
      {
        content: (
          <Dashboard
            key="dashboard"
            navigator={this.props.navigator}
          />
        ),
        tab: (
          <Tab
            key="dashboard"
            label="Dashboard"
            icon="ion-ios-speedometer"
          />
        )
      },
      {
        content: (
          <Trips
            key="trips"
            navigator={this.props.navigator}
          />
        ),
        tab: (
          <Tab
            key="trips"
            label="Trips"
            icon="ion-ios-git-compare"
          />
        )
      },
      {
        content: (
          <Cost
            key="cost"
            navigator={this.props.navigator}
          />
        ),
        tab: (
          <Tab
            key="cost"
            label="Cost"
            icon="ion-ios-cash"
          />
        )
      },
      {
        content: (
          <VehicleList
            key="vehicleList"
            navigator={this.props.navigator}
          />
        ),
        tab: (
          <Tab
            key="vehicleList"
            label="Vehicles"
            icon="ion-ios-bus"
          />
        )
      },
      {
        content: (
          <About
            key="about"
            navigator={this.props.navigator}
          />
        ),
        tab: (
          <Tab
            key="about"
            label="About"
            icon="ion-ios-information-circle-outline"
          />
        )
      }
    ];
  }

  render() {
    return (
      <Page>
        <Tabbar renderTabs={this.renderTabs.bind(this)} />
      </Page>
    )
  }
}

// export default Tabs;
export default Main;
