import React from "react";
import { Page, Toolbar, ToolbarButton, BackButton } from "react-onsenui";
import Keyboard from 'react-simple-keyboard';
import 'react-simple-keyboard/build/css/index.css';
import "./NumericInputForm.css"

class NumericInputForm extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            layoutName: "default",
            layout: {
                default: ["1 2 3", "4 5 6", "7 8 9", ". 0 {bksp}"]
            },
            input: props.initVal,
            firingCount: 0,
            name: props.name
        }
    }

    onChange = input => {
        var _firingCount = this.state.firingCount;
        _firingCount++;
        this.setState({ firingCount: _firingCount })

        if (this.state.firingCount === 1) {
            this.setState({
                input: input
            });
        }
        if (this.state.firingCount === 2) {
            this.keyboard.setInput(this.state.input);
            this.setState({
                firingCount: 0
            });
        }

    };

    onKeyPress = button => {
        // console.log("Button pressed", button);
        /**
         * If you want to handle the shift and caps lock buttons
         */
        if (button === "{shift}" || button === "{lock}") this.handleShift();
    };

    handleShift = () => {
        let layoutName = this.state.layoutName;

        this.setState({
            layoutName: layoutName === "default" ? "shift" : "default"
        });
    };

    handleClickOK = () => {
        const { name, input } = this.state;
        this.props.setInputValue({ name: name, value: input });
        this.props.navigator.popPage();
    }

    onChangeInput = event => {

        // let input = event.target.value;
        // console.log("onChangeInput", input)

        // this.setState(
        //     {
        //         input: input
        //     },
        //     () => {
        //         this.keyboard.setInput(input);
        //     }
        // );
    };

    render() {
        return (
            <Page renderToolbar={() => {
                return <Toolbar>
                    <div className="left"><BackButton>Cancel</BackButton></div>
                    <div className="center">{this.props.label}</div>
                    <div className="right">
                        <ToolbarButton onClick={() => this.handleClickOK()}>OK</ToolbarButton>
                    </div>
                </Toolbar>
            }}>
                <input
                    className="large-numeric-input"
                    value={this.state.input}
                    onChange={e => this.onChangeInput(e)}
                />
                <div className="keyboard-container">
                    <Keyboard
                        display={{
                            '{bksp}': 'DEL'
                        }}
                        physicalKeyboardHighlight={true}
                        physicalKeyboardHighlightBgColor={"#9ab4d0"}
                        keyboardRef={r => (this.keyboard = r)}
                        layout={this.state.layout}
                        layoutName={this.state.layoutName}
                        onChange={input => this.onChange(input)}
                        onKeyPress={button => this.onKeyPress(button)}
                    />
                </div>
            </Page>
        );
    }

}

export default NumericInputForm;