import * as ACTIONS from "../../saga/actions";

const initialState = {
  token: null,
  loading: false,
  hasErr: false
};

export default (state = initialState, action) => {
  switch (action.type) {
    case ACTIONS.LOGIN:
      return {
        ...state,
        loading: true,
        hasErr: false
      };

    case ACTIONS.LOGIN_OK:
      return {
        ...state,
        loading: false,
        hasErr: false,
        token: action.data.token
      };
    case ACTIONS.LOGIN_ERR:
      return {
        ...state,
        loading: false,
        hasErr: true
      };
    default:
      return {
        ...state
      };
  }
};
