import React from "react";
import { Page, Toolbar, ToolbarButton, BackButton, ListItem, AlertDialog } from "react-onsenui";
import NumericInputForm from "../trip/NumericInputForm";
import "./Cost.css"

class Cost extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            isDemoDlgOpen: false,
            fixedCostPerVehicle: 342.63,
            overhead: 354.70
        }
    }

    goInputNumValue = (name, label, setInputValue, initVal) => {
        this.props.navigator.pushPage({
            comp: NumericInputForm,
            props: { key: name, name: name, label: label, setInputValue, initVal: initVal }
        });
    }

    setInputValue = (input) => {
        const { name, value } = input;
        this.setState({
            [name]: value
        });
    }

    handleClickSave = () => {
        this.setState({ isDemoDlgOpen: true })
    }

    render() {

        const { tripRatePerMTPerVehicle, fixedCostPerVehicle, overhead } = this.state;

        const DemoDlg = () => <AlertDialog isOpen={this.state.isDemoDlgOpen} cancelable>
            <div className="alert-dialog-title">Prototype</div>
            <div className="alert-dialog-content">
                This is a prototype app! Please contact our sale engineer.
    </div>
        </AlertDialog>

        return (
            <Page renderToolbar={() => {
                return <Toolbar>
                    <div className="left"><BackButton>Back</BackButton></div>
                    <div className="center">Cost</div>
                    <div className="right"><ToolbarButton onClick={() => this.handleClickSave()}>Save</ToolbarButton></div>
                </Toolbar>
            }}>
                <DemoDlg />
                <div className="cost-container">

                    <ListItem modifier="longdivider chevron" key="fixedCostPerVehicle"
                        onClick={() => this.goInputNumValue("fixedCostPerVehicle", "Fixed cost/Vehicle", this.setInputValue, fixedCostPerVehicle)}>

                        <div className="cost-list-container">
                            <div className="cost-list-header">
                                Fixed Cost per Vehicle
                            </div>
                            <div className="cost-list-value">
                                MYR {fixedCostPerVehicle}
                            </div>
                        </div>
                    </ListItem>

                    <ListItem modifier="longdivider chevron" key="overhead"
                        onClick={() => this.goInputNumValue("overhead", "Overhead/Vehicle", this.setInputValue, overhead)}>

                        <div className="cost-list-container">
                            <div className="cost-list-header">
                                Overhead per Vehicle
                            </div>
                            <div className="cost-list-value">
                                MYR {overhead}
                            </div>
                        </div>
                    </ListItem>

                </div>
            </Page>
        )
    }
}

export default Cost;