import React from "react";

import {
  Page,
  Toolbar,
  BackButton,
  List,
  ListItem,
  Radio
} from "react-onsenui";

class VehicleRegList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedVehicleReg: ""
    };
  }

  componentDidMount() {
    const { initVal } = this.props;
    this.setState({ selectedVehicleReg: initVal });
  }

  handleClickVehicleReg = val => {
    this.setState({ selectedVehicleReg: val });
    this.props.setVehicleReg(val);
  };

  renderToolbar() {
    return (
      <Toolbar>
        <div className="left">
          <BackButton />
        </div>
        <div className="center">Select a vehicle</div>
      </Toolbar>
    );
  }

  render() {
    
    const { selectedVehicleReg } = this.state;
    
    var vehicles = [
        "SELECT",
        "RX7966",
        "RN2666",
        "RN6766",
        "RN5666",
        "RQ7366",
        "RN9866",
        "RQ7566",
        "RX8466",
        "RX5366",
        "RX9466",
        "RX7166",
        "RAA7166",
        "RAB3566",
        "RX7366",
        "RAB1566",
        "RAB3166",
        "BJH9441",
        "RAB3266"
    ]; 

    return (
      <Page renderToolbar={() => this.renderToolbar()}>
        <List 
        dataSource={vehicles} 
        renderRow={(vehicle, idx) => (
            <ListItem
            key="right"
            tappable
            onClick={this.handleClickVehicleReg.bind(this, vehicle)}
          >
            <label className="left">
              <Radio
                inputId="right"
                name="right"
                onChange={this.handleClickVehicleReg.bind(this, vehicle)}
                checked={selectedVehicleReg === vehicle}
              />
            </label>
            <label htmlFor="lwb" className="center">
            {vehicle}
            </label>
          </ListItem>
        )}
        >         
        </List>
      </Page>
    );
  }
}

export default VehicleRegList;
