import React from "react";
import { Page, List, ListItem, Radio, Toolbar, BackButton } from "react-onsenui";

class ReportTypes extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            selectedReport: ""
        }
    }

    componentDidMount() {
        const { initialValue } = this.props;
        this.setState({ selectedReport: initialValue });
    }

    handleClickSelectReport = (value) => {
        this.setState({ selectedReport: value });
        this.props.setReport(value);
    }

    renderToolbar = () => {
        return <Toolbar>
            <div className="left"><BackButton>Cancel</BackButton></div>
            <div className="center">Select report</div>
        </Toolbar>
    }

    render() {
        const { selectedReport } = this.state;

        var reports = [
            "Today",
            "Yesterday",
            "Last 30 days",
            "Last 90 days",
            "Last 180 days"
        ];

        return (
            <Page renderToolbar={() => this.renderToolbar()}>
                <List
                    dataSource={reports}
                    renderRow={(report, index) => (
                        <ListItem
                            key={index}
                            tappable
                            onClick={this.handleClickSelectReport.bind(this, report)}
                        >
                            <label className="left">
                                <Radio
                                    inputId="right"
                                    name="right"
                                    onChange={this.handleClickSelectReport.bind(this, report)}
                                    checked={selectedReport === report}
                                />
                            </label>
                            <label htmlFor="lwb" className="center">
                                {report}
                            </label>
                        </ListItem>
                    )}
                >
                </List>
            </Page>
        );
    }
}

export default ReportTypes;