import React from "react";
import { Page, Toolbar, ToolbarButton, BackButton, AlertDialog } from "react-onsenui";

class Form extends React.Component {

    state = {
        isDemoDlgOpen: false
    }

    handleClickSave = () => {
        this.setState({ isDemoDlgOpen: true })
    }

    render() {
        const { vehicle } = this.props;
        const DemoDlg = () => <AlertDialog isOpen={this.state.isDemoDlgOpen} cancelable>
            <div className="alert-dialog-title">Prototype</div>
            <div className="alert-dialog-content">
                This is a prototype app! Please contact our sale engineer.
    </div>
        </AlertDialog>

        return <Page renderToolbar={() => {
            return <Toolbar>
                <div className="left"><BackButton>Cancel</BackButton> </div>
                <div className="center">{vehicle.registrationNo}</div>
                <div className="right"><ToolbarButton onClick={() => this.handleClickSave()}>Save</ToolbarButton></div>
            </Toolbar>
        }}>
            <DemoDlg />
            <div className="form-container">

            </div>
        </Page>
    }
}

export default Form;