import React from "react";
import { Page, ListItem, Icon } from "react-onsenui";
import ReportTypes from "./ReportTypes";
import "./Dashboard.css"
import faker from "faker";

class Dashboard extends React.Component {
    // var reports = [
    //     {type: "Today"},
    //     {type: "Last 30days"},
    //     {type: "Last 90days"},
    //     {type: "Last 180days"}
    // ];

    constructor(props) {
        super(props);
        this.state = {
            report: "Today"
        }
    }

    setReport = (report) => {
        this.setState({ report: report })
    }

    handleClickSelectReport = () => {
        this.props.navigator.pushPage({
            comp: ReportTypes,
            props: { key: "report_types_view", initialValue: this.state.report, setReport: this.setReport }
        });
    }

    render() {

        var vehicles = [
            "RX7966",
            "RN2666",
            "RN6766",
            "RN5666",
            "RQ7366",
            "RN9866",
            "RQ7566",
            "RX8466",
            "RX5366",
            "RX9466",
            "RX7166",
            "RAA7166",
            "RAB3566",
            "RX7366",
            "RAB1566",
            "RAB3166",
            "BJH9441",
            "RAB3266"
        ];

        var gainersProfit = ["1500.10", "1200.30", "1000.90", "900.35", "700.15"];
        var losersLosses = ["-1000.05", "-930.50", "-800.90", "-700.35", "-700.15"];

        const arrGainers = [1, 2, 3, 4, 5].map((_, index) => < div className="dashboard-list-wrapper" key={index} >
            <div className="dashboard-list-index">{index + 1}. </div>
            <div className="dashboard-list-name">{vehicles[index]}</div>

            <div className="dashboard-list-value">
                <Icon
                    style={{ color: "#20bf6b" }}
                    size={{ default: 25, material: 27 }}
                    icon={{ default: 'ion-ios-arrow-dropup-circle', material: 'md-arrow-dropup' }}
                /><span className="dashboard-currency-symbol">MYR</span> <span className="dashboard-list-profit">{gainersProfit[index]}</span>
            </div>

        </div >);

        const arrLosers = [1, 2, 3, 4, 5].map((_, index) => < div className="dashboard-list-wrapper" key={index} >
            <div className="dashboard-list-index">{index + 1}. </div>
            <div className="dashboard-list-name">{vehicles[index]}</div>

            <div className="dashboard-list-value">
                <Icon
                    style={{ color: "#ff5e57" }}
                    size={{ default: 25, material: 27 }}
                    icon={{ default: 'ion-ios-arrow-dropdown-circle', material: 'md-arrow-dropup' }}
                /><span className="dashboard-currency-symbol">MYR</span>  <span className="dashboard-list-losses">{losersLosses[index]}</span>
            </div>

        </div >);


        return <Page>
            <div className="dashboard-container">

                <ListItem modifier="longdivider chevron" onClick={() => this.handleClickSelectReport()}>
                    <div className="left">Report</div>
                    <div className="right">{this.state.report}</div>
                </ListItem>

                <div className="dashboard-kpi-wrapper-revenue">
                    <div className="dashboard-kpi-value">MYR 0.00</div>
                    <div className="dashboard-kpi-header">Revenue</div>
                </div>

                <div className="dashboard-kpi-wrapper-pink">
                    <div className="dashboard-kpi-value">MYR 0.00</div>
                    <div className="dashboard-kpi-header">Direct Variable Cost</div>
                </div>

                <div className="dashboard-kpi-wrapper-pink">
                    <div className="dashboard-kpi-value">MYR 0.00</div>
                    <div className="dashboard-kpi-header">Direct Fixed Cost</div>
                </div>

                <div className="dashboard-kpi-wrapper-green1">
                    <div className="dashboard-kpi-value">MYR 0.00</div>
                    <div className="dashboard-kpi-header">Gross Profit</div>
                </div>

                <div className="dashboard-kpi-wrapper-gray">
                    <div className="dashboard-kpi-value">MYR 0.00</div>
                    <div className="dashboard-kpi-header">Expenditures</div>
                </div>

                <div className="dashboard-kpi-wrapper-green1">
                    <div className="dashboard-kpi-value">MYR 0.00</div>
                    <div className="dashboard-kpi-header">Net Profit</div>
                </div>

                <div className="dashboard-category">
                    <div className="dashboard-category-header">Top 5 Gainers</div>
                    {arrGainers}
                </div>

                <div className="dashboard-category">
                    <div className="dashboard-category-header">Top 5 Losers</div>
                    {arrLosers}
                </div>

            </div>

        </Page >
    }
}

export default Dashboard;