export const LOGIN =
  "fleetms.io.LOGIN";
export const login = data => ({
  type: LOGIN,
  data
});

export const LOGIN_OK =   "fleetms.io.LOGIN_OK";
export const loginOK = data => ({
  type: LOGIN_OK,
  data
});

export const LOGIN_ERR =
  "fleetms.io.LOGIN_ERR";
export const loginErr = data => ({
  type: LOGIN_ERR,
  data
});