import React from "react";
import { Page, List, ListItem, Toolbar, ToolbarButton, Icon } from "react-onsenui";
import moment from "moment";
import faker from "faker";
import View from "../view/View";
import Form from "../new/FormContainer";

import "./Trips.css"

class Trip extends React.Component {

    state = {
        dataSource: null
    }

    componentWillMount() {
        var dataSource = this.getDemoDatum();
        this.setState({ dataSource: dataSource });
    }

    onViewTrip = (trip) => {
        this.props.navigator.pushPage({
            comp: View,
            props: { key: "trip_view", trip: trip }
        });
    }

    onNewTrip = () => {
        this.props.navigator.pushPage({
            comp: Form,
            props: { key: "trip_form" }
        });
    }

    getDemoDatum = () => {
        var vehicles = [
            "SELECT",
            "RX7966",
            "RN2666",
            "RN6766",
            "RN5666",
            "RQ7366",
            "RN9866",
            "RQ7566",
            "RX8466",
            "RX5366",
            "RX9466",
            "RX7166",
            "RAA7166",
            "RAB3566",
            "RX7366",
            "RAB1566",
            "RAB3166",
            "BJH9441",
            "RAB3266"
        ];

        var dataSource = new Array(19).fill().map(
            (item, index) => {
                return {
                    id: index,
                    datetime: faker.date.past(),
                    vehicleReg: vehicles[index],
                    driverName: `${faker.name.firstName()} ${faker.name.lastName()}`,
                    tripsCount: 1,
                    tripDistTravel: faker.finance.amount(),
                    tripRatePerVolume: faker.random.boolean() ? 74.00 : 145.00,
                    tripVolume: faker.finance.amount(),
                    tripCostFuel: 570.00,
                    tripCostToll: 120.00,
                    tripCostDriverIncentive: 427.00,
                    tripCostTyres: 142.50,
                    tripCostMaintenance: 190.00,
                    grossProfitOrLoss: faker.random.boolean() ? faker.finance.amount() : -faker.finance.amount()
                }
            }
        );
        return dataSource;
    }

    handleDemo = () => {
        var dataSource = this.getDemoDatum();
        this.setState({ dataSource: dataSource });
    }


    render() {


        return <Page renderToolbar={() => {
            return <Toolbar>
                <div className="left">
                    <ToolbarButton onClick={() => this.handleDemo()}>Demo</ToolbarButton>

                </div>
                <div className="center">Trips</div>
                <div className="right">
                    <ToolbarButton onClick={() => this.onNewTrip()}>New</ToolbarButton>
                </div>
            </Toolbar>
        }}>
            <List
                dataSource={this.state.dataSource.reverse()}
                renderRow={(trip, idx) => (
                    <ListItem modifier="longdivider chevron" tappable key={trip.id} onClick={() => this.onViewTrip(trip)}>
                        <div className="trips-list-container">
                            <div className="trips-list-left-wrapper">
                                <div className="trips-list-vehicleReg-header">
                                    Vehicle Registration No
                    </div>
                                <div className="trips-list-vehicleReg">
                                    {trip.vehicleReg}
                                </div>
                                <div className="trips-list-datetime">
                                    <Icon
                                        size={{ default: 16, material: 18 }}
                                        icon={{ default: 'ion-ios-person', material: 'md-person' }}
                                    /> {trip.driverName} {moment(trip.datetime).format('DD/MM/YYYY')}
                                </div>
                            </div>
                            <div className="trips-list-right-wrapper">
                                {trip.grossProfitOrLoss > 0 ?
                                    <div>
                                        <Icon
                                            style={{ color: "#20bf6b" }}
                                            size={{ default: 25, material: 27 }}
                                            icon={{ default: 'ion-ios-arrow-dropup-circle', material: 'md-arrow-dropup' }}
                                        /> <span className="trip-list-profit">MYR {trip.grossProfitOrLoss}</span></div>
                                    :
                                    <div>
                                        <Icon
                                            style={{ color: "#fc5c65" }}
                                            size={{ default: 25, material: 27 }}
                                            icon={{ default: 'ion-ios-arrow-dropdown-circle', material: 'md-arrow-dropup' }}
                                        /> <span className="trip-list-losses">MYR {trip.grossProfitOrLoss}</span></div>
                                }

                            </div>
                        </div>
                    </ListItem>
                )}
            />
        </Page>
    }

}

export default Trip;