import React from "react";
import { Page, Toolbar, BackButton, ToolbarButton, ListItem, AlertDialog, Button } from "react-onsenui";
import DatePicker from "react-mobile-datepicker";
import moment from "moment";
import VehicleRegList from "../VehicleRegList";
import TripsCountList from "../TripsCountList";
import NumericInputForm from "../NumericInputForm";
import "./Form.css";

class Form extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            isDemoDlgOpen: false,
            isDateDialogOpen: false,
            vehicleReg: "SELECT",
            datetime: new Date(),
            tripsCount: 1,
            tripDistTravel: 0.0,
            tripVolume: 0.0,
            tripRatePerVolume: 75.0,
            tripCostFuel: 0.0,
            tripCostToll: 0.0,
            tripCostDriverIncentive: 0.0,
            tripCostTyres: 0.0,
            tripCostMaintenance: 0.0
        }
    }

    goSelectVehicleRegs(key, setVehicleReg, initVal) {
        this.props.navigator.pushPage({
            comp: VehicleRegList,
            props: { key, setVehicleReg, initVal }
        });
    }

    setVehicleReg = val => {
        this.setState({
            vehicleReg: val
        });
    };

    goSelectTripsCount(key, setTripsCount, initVal) {
        this.props.navigator.pushPage({
            comp: TripsCountList,
            props: { key, setTripsCount, initVal }
        });
    }

    setTripsCount = val => {
        this.setState({
            tripsCount: val
        });
    };

    goInputNumValue = (name, label, setInputValue, initVal) => {
        this.props.navigator.pushPage({
            comp: NumericInputForm,
            props: { key: name, name: name, label: label, setInputValue, initVal: initVal }
        });
    }

    setInputValue = (input) => {
        const { name, value } = input;
        this.setState({
            [name]: value
        });
    }

    handleSelectDateClick = () => {
        this.setState({ isDateDialogOpen: !this.state.isDateDialogOpen })
    }

    handleSelectDate = (datetime) => {
        this.setState({ datetime, isDateDialogOpen: false });
    }

    handleSelectDateCancel = () => {
        this.setState({ isDateDialogOpen: false });
    }

    handleClickSave = () => {
        this.setState({ isDemoDlgOpen: true })
    }

    render() {

        const { vehicleReg, tripsCount, tripDistTravel,
            tripVolume, tripRatePerVolume, tripCostFuel, tripCostToll,
            tripCostDriverIncentive, tripCostTyres, tripCostMaintenance
        } = this.state;

        const DemoDlg = () => <AlertDialog isOpen={this.state.isDemoDlgOpen} cancelable>
            <div className="alert-dialog-title">Prototype</div>
            <div className="alert-dialog-content">
                This is a prototype app! Please contact our sale engineer.
        </div>
        </AlertDialog>

        return <Page renderToolbar={() => {
            return <Toolbar>
                <div className="left"><BackButton>Cancel</BackButton></div>
                <div className="center">New Trip</div>
                <div className="right">
                    <ToolbarButton onClick={() => this.handleClickSave()}>Save</ToolbarButton>
                </div>
            </Toolbar>
        }}>
            <DemoDlg />
            <div className="form-container">
                <ListItem key="date" onClick={this.handleSelectDateClick} tappable>
                    <div className="left">Date</div>
                    <div className="right">
                        {moment(this.state.datetime).format("DD-MM-YYYY")}
                        <DatePicker
                            value={this.state.datetime}
                            isOpen={this.state.isDateDialogOpen}
                            onSelect={this.handleSelectDate}
                            onCancel={this.handleSelectDateCancel}
                            theme="ios"
                            confirmText="OK"
                            cancelText="Cancel"
                        />
                    </div>
                </ListItem>

                <ListItem modifier="longdivider chevron" key="vehicleReg" onClick={() => this.goSelectVehicleRegs("vehicleReg", this.setVehicleReg, "SELECT")}>
                    <div className="left">
                        Vehicle Reg. No
                    </div>
                    <div className="right">
                        {vehicleReg}
                    </div>
                </ListItem>

                <ListItem modifier="longdivider chevron" key="tripsCount" onClick={() => this.goSelectTripsCount("tripsCount", this.setTripsCount, 1)}>
                    <div className="left">
                        Trips count
                    </div>
                    <div className="right">
                        {tripsCount}
                    </div>
                </ListItem>

                <ListItem modifier="longdivider chevron" key="tripDistTravel"
                    onClick={() => this.goInputNumValue("tripDistTravel", "Distance traveled (km)", this.setInputValue, tripDistTravel)}>
                    <div className="left">
                        Distance travelled
                    </div>
                    <div className="right">
                        {tripDistTravel} km
                    </div>
                </ListItem>

                <ListItem modifier="longdivider chevron" key="tripVolume"
                    onClick={() => this.goInputNumValue("tripVolume", "Trip Vol. (MT)", this.setInputValue, tripVolume)}>
                    <div className="left">
                        Trip Volume
                    </div>
                    <div className="right">
                        {tripVolume} MT
                    </div>
                </ListItem>

                <ListItem modifier="longdivider chevron" key="tripRatePerVolume"
                    onClick={() => this.goInputNumValue("tripRatePerVolume", "Rate", this.setInputValue, tripRatePerVolume)}>
                    <div className="left">
                        Rate per volume (MT)
                    </div>
                    <div className="right">
                        <span className="currency-label">MYR</span> {tripRatePerVolume}
                    </div>
                </ListItem>

                <ListItem modifier="longdivider chevron" key="tripCostFuel"
                    onClick={() => this.goInputNumValue("tripCostFuel", "Fuel Cost", this.setInputValue, tripCostFuel)}>
                    <div className="left">
                        Fuel Cost
                    </div>
                    <div className="right">
                        <span className="currency-label">MYR</span> {tripCostFuel}
                    </div>
                </ListItem>


                <ListItem modifier="longdivider chevron" key="tripCostToll"
                    onClick={() => this.goInputNumValue("tripCostToll", "Toll Cost", this.setInputValue, tripCostToll)}>
                    <div className="left">
                        Toll Cost
                    </div>
                    <div className="right">
                        <span className="currency-label">MYR</span> {tripCostToll}
                    </div>
                </ListItem>

                <ListItem modifier="longdivider chevron" key="tripCostDriverIncentive"
                    onClick={() => this.goInputNumValue("tripCostDriverIncentive", "Driver incentive", this.setInputValue, tripCostDriverIncentive)}>
                    <div className="left">
                        Driver incentive
                    </div>
                    <div className="right">
                        <span className="currency-label">MYR</span> {tripCostDriverIncentive}
                    </div>
                </ListItem>

                <ListItem modifier="longdivider chevron" key="tripCostTyres"
                    onClick={() => this.goInputNumValue("tripCostTyres", "Tyres", this.setInputValue, tripCostTyres)}>
                    <div className="left">
                        Tyres cost
                    </div>
                    <div className="right">
                        <span className="currency-label">MYR</span> {tripCostTyres}
                    </div>
                </ListItem>

                <ListItem modifier="longdivider chevron" key="tripCostMaintenance"
                    onClick={() => this.goInputNumValue("tripCostMaintenance", "Maintenance", this.setInputValue, tripCostMaintenance)}>
                    <div className="left">
                        Maintenance cost
                    </div>
                    <div className="right">
                        <span className="currency-label">MYR</span> {tripCostMaintenance}
                    </div>
                </ListItem>

            </div>
        </Page>
    }
}

export default Form;